.WT_Calendar_Header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  //height: 142px;
}

.WT_Calendar_Header_businessRender {
  position: relative;
  width: calc(100% - 54px);
  height: 44px;
  margin-left: 54px;
  overflow-x: auto;
  overflow-y: hidden;
  &::-webkit-scrollbar {
    width: 0;
    height: 6px;
  }
  &::-webkit-scrollbar-thumb {
    background: #9f9fa0;
    border-radius: 15px;
    box-shadow: inset 0 0 5px rgba(162, 139, 139, 0.2);
  }
  &::-webkit-scrollbar-track {
    background: #ededed;
    border-radius: 15px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  }
}

.WT_Calendar_Header_Zone {
  position: absolute;
  left: 0;
  z-index: 10;
  width: 50px;
  height: 44px;
  color: #c1c1c2;
  font-size: 12px;
  line-height: 48px;
  text-align: center;
  background: #fff;
  border-right: 1px solid #f0f0f0;
}
